import React from 'react'
import Page from './Page'
import { Link } from 'react-router-dom'

export const Assistant = (props) => {

    return (
        <Page title="Assistant de donnée">
            <ul>
                <li>
                    <Link replace to={"/assistant/diagnosis"}>Créer une donnée de diagnostic</Link>
                </li>
                <li>
                    <Link replace to={"/assistant/homedata"}>Créer une donnée de maison</Link>
                </li>
                <li>
                    <Link replace to={"/assistant/equipments"}>Créer une donnée d'équipement</Link>
                </li>
                <li>
                    <Link replace to={"/assistant/works"}>Créer une donnée de travaux</Link>
                </li>
                <li>
                    <Link replace to={"/assistant/maintenances"}>Créer une donnée d'entretien</Link>
                </li>
            </ul>
        </Page>
    )
}

export default Assistant


