import React, {Component} from 'react'
import classNames from 'classnames'
// import "./Preferences.scss"
import button from '../../styles/buttons.scss'
import style from '../../styles/Preferences.sass'
import Page from './Page'

import Exporter from '../../scripts/XMLDataFile.js'

import { markAsShown } from '../../scripts/onboarding'

import { forceFileDownload } from '../../tools'

import { DOCTYPE_META } from '../../constants'

export const Preferences = (props) => {

    const resetOnboarding = () => {
        markAsShown(false)
        .then(() => {
            props.history.push('/')
        })
    }

    return (
        <Page title="Préférences">
            <article>
                <h2 className={style.title}>Exporter votre HOMEBOOK</h2>
                <div className={style.article}>
                    <p>
                        Si vous disposez déjà d’un carnet numérique du logement, vous pouvez l’intégrer
                        dans votre homebook par simple importation du fichier de type « .zip » fourni par l’opérateur
                        de ce carnet  De la même manière, vous pouvez exporter le carnet numérique homebook.
                        Cette action générera un fichier « .zip » sur votre ordinateur.
                    </p>

                    <button className={classNames(button.button, button.default)} disabled>
                        <i className="ion-ios-upload-outline"></i> IMPORTER VOTRE CARNET NUMÉRIQUE DU LOGEMENT
                    </button>
                    <button className={classNames(button.button, button.default)} onClick={e => exportHomebook()}>
                        <i className="ion-ios-download-outline"></i> EXPORTER VOTRE CARNET NUMÉRIQUE DU LOGEMENT
                    </button>
                    
                </div>
            </article>

            <article>
                <h2 className={style.title}>Reset Onboarding</h2>
                <div className={style.article}>
                    <p>
                        A votre première connexion, nous vous avons présenté les grandes
                        fonctionnalités : vous pouvez les consulter à nouveau avec ce bouton.
                    </p>

                    <button className={classNames(button.button, button.assertive)} onClick={() => resetOnboarding()}><i className="ion-ios-refresh-outline"></i>Revoir la présentation de Homebook</button>
                </div>
            </article>
        </Page>
    )
}

const exportHomebook = () => {
    Exporter()
    .then(link => forceFileDownload(link, 'homebook.zip'))
}

export default Preferences


