import React, {Component} from 'react'
import classNames from 'classnames'

import { createOptionsFromArray } from '../../tools'
import button from '../../styles/buttons.scss'
import style from '../../styles/DocumentViewModal.sass'

export const FileTypeNotReadable = (props) => {

    const close = e => {
        e.stopPropagation()
        //close the portal
        props.closePortal()
    }

    return (
        <div className={style.main} onClick={e => e.stopPropagation()}>
            <div className={style.header}>
                <h1>Informations</h1>

                <button
                    onClick={e => close(e)}
                    className={classNames(button.button, button.stable, button.clear)}>
                    <i className="ion-ios-close-empty"></i>
                </button>
            </div>
            <div className={style.content}>
                <div>Impossible de lire ce type de fichier, veuillez le télécharger afin de le lire sur votre ordinateur</div>
            </div>
        </div>
    )
}

export default FileTypeNotReadable



