import React, {Component} from 'react'
import classNames from 'classnames'
import moment from 'moment'
import style from '../../styles/DocumentList.sass'

import {VIEW_GRID, VIEW_LIST} from '../../constants'

export const DocumentList = (props) => {

    return (
        <div className={classNames(style.list, {
            [style.viewList]: props.view === VIEW_LIST,
            [style.viewGrid]: props.view === VIEW_GRID
        })}>
            {props.children.map((item, key) => (
                <div key={key} onClick={e => props.onClickItem(item, e)} className={classNames(style.document, {[style.selected]: item.selected})}>
                    {props.onSelectItem && item.selected && (
                        <i className={classNames(style.checkbox, "ion-android-checkbox-outline")} onClick={(e) => {e.stopPropagation(); props.onSelectItem(item, e);}}></i>
                    )}

                    {props.onSelectItem && !item.selected && (
                        <i className={classNames(style.checkbox, "ion-android-checkbox-outline-blank")} onClick={(e) => {e.stopPropagation(); props.onSelectItem(item, e);}}></i>
                    )}

                    <div className={style.content}>
                        <DocIcon doc={item}/>
                    </div>

                    <div
                    className={style.name}
                    title={item.name}>
                        {item.name}
                    </div>

                    <div className={style.date}>
                        <span className={style.dateText}>Dernière modification : </span>
                        <span>{moment(item.updated_at).format('DD/MM/YYYY')}</span>
                    </div> 
                </div> 
            ))}
        </div>
    )
}

class DocIcon extends Component {

    constructor(props, context){
        super(props, context)

        this.state = {
            icon: this.getItemIcon()
        }
    }

    componentWillReceiveProps(newProps) {
        this.state = {
            icon: this.getItemIcon()
        }
    }

    getItemIcon() {
        const item = this.props.doc
        const type = item.type || 'file'
        let icon = '/images/file'

        if(type === 'file') {
            if(item.class === 'image' && item.links && item.links.small) {
                cozy.client.fullpath(item.links.small)
                .then(path => this.setState({icon: path}))
            }
        } else {
            icon = '/images/'

            switch (item.name) {
                case 'Diagnostics':
                    icon += 'folder-diagnostic'
                    break

                case 'Documents de propriété':
                    icon += 'folder-document-de-propriete'
                    break

                case 'Équipements':
                    icon += 'folder-equipements'
                    break

                case 'Travaux':
                    icon += 'folder-travaux'
                    break

                case 'Entretiens':
                    icon += 'folder-entretien'
                    break

                case 'Usages':
                    icon += 'folder-document-de-propriete' //TODO: change to the correct icon name
                    break

                default :
                    icon += "folder"
            }
        }

        if(item.selected) icon += '.selected'
        return icon + '.png'
    }

    iconErr() {
        if(this.props.doc.class === 'image') {
            cozy.client.files.getDownloadLinkById(this.props.doc.id)
            .then(link => cozy.client.fullpath(link))
            .then(path => this.setState({icon: path}))            
        }
    }

    render(){
        return (
            <img src={this.state.icon} onError={(e) => this.iconErr(e)}></img>
        )
    }

}


export default DocumentList


